import * as React from 'react';
import {Component} from 'react';
import {PropTypes as Prop}  from "react";

export const TYPE_INFO = {
    type: "text",
    name: "Text",
    description: "Display content of a datasource as plain text",
    settings: [
        {
            id: 'datasource',
            name: 'Datasource',
            type: 'datasource',
            description: "Datasource to get the text"
        }
    ]
};

export class Widget extends Component {

    render() {
        const props = this.props;
        const data = props.getData(this.props.config.datasource);

        if(!data || data.length == 0) {
            return <p>No data</p>
        }
        
        return <p>{JSON.stringify(data)}</p>
    }
}

// TODO: Move to core, for simple reuse
Widget.propTypes = {
    config: Prop.object.isRequired
};


